.project__container{
    background-color: #333;
    min-height: 80vh;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.project__text{
    color:#fff;
    text-align: center;
    margin: 10px 0px;
}
.project__text>h2{
    font-size: 2.2rem;
    font-weight: 700;
    margin: 8px 0px;
    padding: 5px 0px;
}
.project__text>p{
    font-size: 1.4rem;
    font-weight: 400;
    padding: 10px 0px;
}
.project__cards{
    width:100%;
    display: flex;
    align-items: center;
    justify-content:center;
    margin: 20px 0px;
    gap:30px
}
.codepen__card>a,.github__card>a{
    text-decoration: none;
    font-size: 1.5rem;
    color: #333333;
    font-weight: 600;
    margin: 5px 0px;
}
.codepen__card{
    background-color: #ffffff;
    width:400px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0px;     
    border-radius: 10px; 
}
.icon{
    font-size: 4rem;
    margin: 10px 0px;

}
.github__card{
    background-color: #ffffff;
    
    width:400px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0px;
    border-radius: 10px;
}
.codepen__card:hover,.github__card:hover{
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    
}
@media only screen and (max-width:900px){
    .project__cards{
        flex-wrap: wrap;

    }
 
}