.about__img>img{
    max-width:80%;
    margin:0 auto;
    text-align: center;
    
}
.about__container{
    background-color:#333;
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
}
.about__content{
    color:#fff;
    line-height: 2.9rem;
}

.about__content>h2{
    font-size: 2rem;
    font-weight:900;
}
.about__content>h3{
    font-size: 1.5rem;
}

.work__btn{
    padding:10px 15px;
    margin: 0px 15px;
    font-size: 1rem;
    margin-top: 10px;
    transition: transform 0.5s ease-in-out;
}
.download__btn{
    background-color: #e74c3c;
    color:#333;
    text-decoration: none;
    font-weight: 600;
    display: inline-block;
    padding:0px 3px;
    margin: 0px 15px;
    font-size: 1rem;
    margin-top: 10px;
    transition: transform 0.5s ease-in-out;
}
.work__btn{
    font-weight: 500;
}
span{
    color:#e74c3c;
}
.about__btn:hover{
    transform: scale(1.2);
}
