.skillContainer__img>img{
    width: 500px;
    text-align: center;
    object-fit: contain;
    flex: 0.4;
    margin-right: 100px;
    border-radius: 5px;
    margin:0px auto;
}
.skillContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
    background-color: #333;
    min-height: 100vh;
    width: 100%;
    /* flex-wrap: wrap; */
    gap:50px;
}
.skillContainer__content{
    flex:0.6;
    margin-top: 10px;
}

.skillContainer__slider1>span>.css-5xe99f-MuiLinearProgress-bar1{
    background-color: rgb(218 48 48);
}
.skillContainer__slider2>span>.css-5xe99f-MuiLinearProgress-bar1{
    background-color: orange;
}
.skillContainer__slider3>span>.css-5xe99f-MuiLinearProgress-bar1{
    background-color: purple;
}
.skillContainer__slider4>span>.css-5xe99f-MuiLinearProgress-bar1{
    background-color: yellow;
}
.skillContainer__slider5>span>.css-5xe99f-MuiLinearProgress-bar1{
    background-color:royalblue;
}
.skillContainer__slider>.MuiLinearProgress-determinate{
    background-color:black;
}
.skillContainer__skillset>h4{
    color:#fff;
    font-weight: 400;
    padding:30px 0px;
}
.skillContainer__content>h1{
    color:#fff;
    font-size: 1.9rem;
    font-weight: 600;
    padding-bottom: 15px;
}

@media  only screen and (max-width:975px) {
    .skillContainer{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .skillContainer__img>img{
        width:100%;
        margin:0px auto;
        text-align: center;
        
    }
    
}