span{
    color:#e74c3c;
}
.header{
    background-color: #333;
    display: flex;
    justify-content: space-between;
    height:100px;
    align-items: center;
    position: sticky;
    top:0;
    left:0;
    z-index: 100;
}
.header__right{
    color:#fff;
    display: flex;
    justify-content: space-between;
    flex:0.6;
    align-items: center;
    transition: transform 200ms ease-in-out;
    
    
}
.header__left{
    color:#fff;
    flex:0.4;
    margin: 0px 10px;
}
.header__btn{
    background-color: #e74c3c;
    color:#fff;
    border:none;
    padding: 10px 15px;
    cursor:pointer;
    font-size: 1rem;
    margin: 2px 0px;
    border-radius: 3px;
    font-weight: 600;
    transition: transform 200ms ease-in-out;
    transition: background-color 0.5s ease-in-out;
 
}
.header__right>a:hover{
    background-color: #e74c3c;
    transform: scale(0.9);
}
.header__btn:hover{
    transform: scale(1.1);
}
.header__right>a{
    margin-right:5px;
    cursor: pointer;
}
.hamburger{
    display: none;
}
/* Media query  */
@media only screen and (max-width:990px) {
    .hamburger{
        display: block;
        margin-right:50px;
    }
    
    .header__right{
        position:fixed;
        top:100px;
        left:-100%;
        display: flex;
        flex-direction: column;
        justify-content:space-around;
        align-items: center;
        width:100%;
        height:100vh;
        background-color:#333;
        padding: 10px 2px;
        text-align: center;
        transition: 0.4s;
        

    }
    .header__right.active{
     left:0%;
    }
}