.contact__Container{
    background-color: #333;
    max-height:100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #fff;
    max-width:700px;
    margin: 10px auto;
    margin-top: 50px;
    border-radius: 5px;
  
}
body{
    background-color: #333;
  }
.contact__text{
    color:#fff;
    padding-bottom: 10px;
}
.contact__text>h2{
    font-size:1.9rem;
    font-weight: 600;
    margin: 20px auto;
    border-bottom:5px solid #e74c3c;
    text-align: center;
    max-width:40%;
}

.contact__form{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 600px;
    height: 100%;
   

}
.contact__text>p{
    font-size: 1.2rem;
    font-weight: 300;
    padding: 10px 0px;
    text-align: center;
    line-height: 2;
}
.contact__inputs{
    display: flex;
    flex-direction: column;
    width:100%;
    
 
}
.contact__inputs>input[type="text"]{
    padding: 10px;
    margin: 4px 0px;
    background-color: #ccd6f6;
}
.contact__inputs>input[type="email"]{
    margin: 6px 0px;
    padding:10px;
    background-color: #ccd6f6;
}
textarea{
    margin:6px 0px;
    padding-top:15px;
    background-color: #ccd6f6;
}
.contact__btn{
    margin: 8px auto;
    text-align: center;
    border: 2px solid #fff;
    width:40%;
    padding: 10px 0px;
    color:#fff;
    font-size: 1rem;
     background-color: transparent;
     margin-top: 20px;
     font-weight: 700;
     border-radius: 3px;
}
.contact__btn:hover{
    background-color: #e74c3c;
    border: 2px solid #e74c3c;
    color: #333;
}
::placeholder{
    color:black;
    font-weight: 700;
}

@media only screen and (max-width:600px) {
    .contact__text>h2{
       
        max-width:35%;
    }
    
}